<template>
  <b-modal
    id="modal-upload-templates"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="md"
    ignore-enforce-focus-selector="span.select2-container"
    title="title"
  >
    <div class="form-group">
      <label for="">Tên thư mục</label>
      <input type="text" v-model="folder_name" required class="form-control" />
    </div>
    <div class="form-group">
      <input type="file" class="form-control" @change="getFile" />
    </div>
    <template #modal-footer>
      <button class="btn btn-primary-soft" @click="store()">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="$bvModal.hide('modal-upload-templates')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
  </b-modal>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      title: "Upload templates",
      file: "",
      folder_name: "",
    };
  },
  methods: {
    show() {
      this.$bvModal.show("modal-upload-templates");
    },
    getFile(e) {
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.file = files[0];
    },

    async store() {
      if (!this.file) {
        alert("Please select a file.");
        return;
      }

      let formData = new FormData();
      formData.append("file", this.file);
      formData.append("folder_name", this.folder_name);

      await axios
        .post("templates", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          console.log(response.data);
        });
    },
  },
};
</script>
