<template>
<div>
    
    <div class="row">
        <div class="col-12">
            <table class="table table-hover">
                <thead>
                    <tr class="table-dark">
                        <th class="rounded-start">#</th>
                        <th>Tên mẫu hợp đồng</th>
                        <th>Tên hiền thị</th>
                        <th>Đường dẫn</th>
                        <th>Hiển thị</th>
                        <th>Lặp dòng</th>
                        <th width="10" class="rounded-end"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in templates" :key="item.temp_id">
                        <td>{{index+1}}</td>
                        <td><span class="text-primary">{{ item.temp_name }}</span></td>
                        <td>{{ item.temp_display }}</td>
                        <td>{{ item.temp_path }}</td>
                        <td>
                            <div class="form-check">
                                <input class="form-check-input" type="checkbox" v-model="item.active" :checked="item.active" @change="changeStatus(item,0)" />
                            </div>
                        </td>
                        <td>
                            <div class="form-check">
                                <input class="form-check-input" type="checkbox" v-model="item.temp_cloneRow" :checked="item.temp_cloneRow" @change="changeStatus(item,1)" />
                            </div>
                        </td>
                        <td>
                            <div class="dropdown">
                                <button class="btn btn-sm border-0" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                    <i class="fa-solid fa-ellipsis-vertical"></i>
                                </button>
                                <ul class="dropdown-menu">

                                    <li>
                                        <a class="dropdown-item hand">
                                           <i class="fa-thin fa-pen-to-square"></i> Cập nhật mẫu</a>
                                    </li>
                                </ul>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
    props: {
        templates: Array
    },
    data() {
        return {
            templateSelected: [],
        }
    },
    methods: {
        async changeStatus(item, type) {
            this.$econtract.showLoading();
            await axios.put(`${API_URL}/templates/changeStatus`, {
                template: item,
                status: type
            }).then(response => {
                if (response.data) {
                    this.$toast.success("Cập nhật dữ liệu thành công");
                    this.$econtract.hideLoading()
                }
            })
        }
    }

}
</script>
