<template>
<div>
    <h1 class="fw-bold title-page">Danh sách mẫu hợp đồng</h1>
    <div class="row mb-2">
        <div class="col-12">
            <div class="input-group">
                <input type="search" placeholder="Nhập tên dịch vụ để tìm kiếm...." class="form-control" v-model="keyword" @keyup.enter="search()" />
                <button class="btn btn-primary-soft" @click="uploadTemplates">
                    <i class="fa-regular fa-cloud-arrow-up"></i> Upload
                </button>
                <button type="button" id="button-addon2" class="btn btn-primary-soft" @click="search()">
                    <i class="fa-solid fa-magnifying-glass"></i> Tìm kiếm
                </button>
            </div>
        </div>
    </div>
    <templateList :templates="templateSearch"></templateList>
    <uploadTemplates ref="uploadTemplates"></uploadTemplates>
</div>
</template>

<script>
import templateList from "@/components/templates/Index.vue"
import uploadTemplates from "@/extends/ModalUpdateTemplates.vue"
import axios from 'axios'
export default {
    components: {
        templateList,
        uploadTemplates
    },
    data() {
        return {
            templates: [],
            keyword: "",
        }
    },
    computed: {
        templateSearch() {
            return this.templates.filter((item) => {
                return item.temp_name.toLowerCase().indexOf(this.keyword.toLowerCase()) !== -1;
            });
        },
    },
    created() {
        this.getTemplates()
    },
    methods: {
        uploadTemplates(){
            this.$refs.uploadTemplates.show();
        },
        search() {

        },
        async getTemplates() {
            this.$econtract.showLoading();
            await axios.get(`/templates/get_all`).then(response => {
                console.log(response);
                this.templates = response.data;
                this.$econtract.hideLoading()
            })
        }
    }
}
</script>
